<template id="navi-template">
      <nav class="navbar navbar-expand-md navbar-light bg-light">
      <a class="navbar-brand" href="#">ORZtools</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav mr-auto">
          <li class="nav-item active" v-on:click="navItemClick('home')">
              <a class="nav-link" href="#">首页 <span class="sr-only">(current)</span></a>
          </li>
          <li class="nav-item" v-on:click="navItemClick('json')">
              <a class="nav-link" href="#">JSON编辑器</a>
          </li>
          <li class="nav-item" v-on:click="navItemClick('qrcode')">
              <a class="nav-link" href="#">二维码生成器</a>
          </li>
          <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              单位转换
              </a>
              <div class="dropdown-menu" aria-labelledby="navbarDropdown">
              <a class="dropdown-item" href="#">长度单位</a>
              <a class="dropdown-item" href="#">时间单位</a>
              <div class="dropdown-divider"></div>
              <a class="dropdown-item" v-on:click="navItemClick('color')" href="#">颜色转换</a>
              </div>
          </li>
          <li class="nav-item">
              <a class="nav-link disabled" href="#" tabindex="-1" aria-disabled="true"></a>
          </li>
          </ul>
          <form class="form-inline my-2 my-lg-0">
          <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
          </form>
        </div>
      </nav>
</template>

<script>
export default {
  name: 'Navigation',
  data () {
    return {
      active: 'home'
    }
  },
  methods: {
    navItemClick: function (item) {
      if (this.active === item) {
        return
      }
      this.active = item
      if (this.active === 'home') {
        this.$router.push('/')
      } else if (this.active === 'json') {
        this.$router.push('/JsonEditor')
      } else if (this.active === 'qrcode') {
        this.$router.push('/QRCode')
      } else if (this.active === 'color') {
        this.$router.push('/ColorConversion')
      }
    }
  }
}
</script>



