<template>
  <div id="qrcode">
    <br/>
    <h1>{{ msg }}</h1>
    <br/>
    <div>
      <textarea id="textarea" v-model="qrstring" cols="50" rows="6" placeholder="input qrcode info"></textarea>
      <button id="generateButton" v-on:click="generate">生成二维码</button>
      <canvas id="canvas"></canvas>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode'

export default {
  name: 'QRCode',
  data () {
    return {
      msg: 'QR CODE',
      qrstring: 'orztools.com'
    }
  },
  mounted () {
    QRCode.toCanvas(document.getElementById('canvas'), this.qrstring, function (error) {
      if (error) console.error(error)
      console.log('success!')
    }
    )
  },
  methods: {
    generate: function (event) {
      QRCode.toCanvas(document.getElementById('canvas'), this.qrstring, function (error) {
        if (error) console.error(error)
        console.log('success!')
      }
      )
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#qrcode {
  margin: 20px;
}

#textarea {
  float: left;
}

#generateButton {
  float: left;
  margin-left: 20px
}

#canvas {
  float: left;
  margin-left: 20px
}

h1, h2 {
  font-weight: normal;
}
</style>



