<template>
  <div class="colorConversion">
    <h1>{{ msg }}</h1>
    十六进制颜色码转换成RGB颜色值：<br>
    <input type="text" v-model="hexVaule">
    <button v-on:click="convertToRgb">转换</button>
    <b>{{ rgbResult }}</b>
    <br>
    <br>
    RGB颜色值转换成十六进制颜色码：<br>
    <input type="text" v-model="rVaule">
    <input type="text" v-model="gVaule">
    <input type="text" v-model="bVaule">
    <button v-on:click="convertToHex">转换</button>
    <b>{{ hexResult }}</b>
  </div>
</template>

<script>
import tinycolor from 'tinycolor2'

export default {
  name: 'ColorConversion',
  data () {
    return {
      msg: 'Color Conversion',
      hexVaule: '#888888',
      rgbResult: 'rgb(136, 136, 136)',
      rVaule: '255',
      gVaule: '255',
      bVaule: '255',
      hexResult: '#ffffff'
    }
  },
  methods: {
    convertToRgb: function (event) {
      var color = tinycolor(this.hexVaule)
      this.rgbResult = color.toRgbString()
    },
    convertToHex: function (event) {
      var rgbString = 'rgb(' + this.rVaule + ', ' + this.gVaule + ', ' + this.bVaule + ')'
      var color = tinycolor(rgbString)
      this.hexResult = '#' + color.toHex()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.colorConversion {
  margin: 20px;
}

h1, h2 {
  font-weight: normal;
}
</style>



